import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Layout } from "antd";
import config from "./../../config";
import styled from "styled-components";

import * as processActions from "./../../reducers/process";
import * as authActions from "./../../reducers/auth";
import { getApiEngines } from "./../../services";

import "./index.css";

import FreeUser from "./../../components/FreeUser";
import ContentCmp from "./../../components/Content";
import HeaderCmp from "./../../components/Header";

import Background from "./background.jpg";

const LayoutWrapper = styled(Layout)`
  background-image: url(${Background}) !important;
  background-size: cover !important;
  background-position: center center !important;
`;

class LandingComponent extends Component {
  state = { view: "signin", visible: false };

  signIn = data => {
    const {
      authAct: { fetchAuth }
    } = this.props;
    fetchAuth(data);
  };
  render() {
    return (
      <LayoutWrapper>
        <HeaderCmp />
        <ContentCmp>
          <FreeUser />
        </ContentCmp>
      </LayoutWrapper>
    );
  }
}

function mapStateToProps(state) {
  return {
    process: state.rootReducer.process,
    location: state.router.location
  };
}

function mapDispatchToProps(dispatch) {
  return {
    processAct: bindActionCreators(processActions, dispatch),
    authAct: bindActionCreators(authActions, dispatch)
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LandingComponent);
