import React, { Component } from "react";
import socketIOClient from "socket.io-client";
import config from "./../config";
import {
  Row,
  Col,
  Upload,
  Divider,
  message,
  Form,
  Input,
  Alert,
  Button,
  Select,
  Icon
} from "antd";
import ImageLoader from "react-loading-image";
import styled from "styled-components";
import { sendFile, getApiEngines } from "./../services";
import functions from "./../utils/functions";

const Dragger = Upload.Dragger;
const FormItem = Form.Item;
const Option = Select.Option;

const FormItemWrapper = styled(FormItem)`
  margin-bottom: 5px !important;
  & {
    .ant-form-item-label {
      line-height: 20px;
    }
    .ant-form-explain {
      color: rgb(24, 144, 255);
    }
  }
`;

const Div = styled.div`
  width: 100%;
  ${props => (props.error ? `  border: 2px dashed #ea2020` : null)};
`;
const DivError = styled.div`
  h2,
  p {
    text-align: center;
  }
`;

const DivButtons = styled.div`
  display: flex;
  justify-content: center;
`;

const IL = styled(ImageLoader)`
  width: 100%;
  height: auto;
`;

class Process extends Component {
  constructor(props) {
    super(props);

    this.state = {
      fileList: [],
      uploading: false,
      processEnd: false,
      showError: false,
      source: null,
      target: null,
      email: null,
      // viewNoty: 'success',
      viewNoty: "process",
      value: undefined,
      _input: null,
      loading: false,
      engines: [],
      selected: {
        engines: []
      }
    };

    this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount = () => {
    this.getEngines();
    this.state._input.focus();
    // const socket = socketIOClient(config.API_SERVER);
    // socket.on("notification", noty => {
    //   console.log(noty);
    // });
  };

  getEngines = async () => {
    this.setState({ loading: true });
    try {
      const engines = await getApiEngines();
      this.setState({ engines: engines.data, loading: false });
    } catch (err) {
      this.setState({ loading: false });
      console.log(err);
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    const self = this;
    const { fileList, target, selected } = this.state;
    const { rol } = this.props;

    const targets = this.getTargets();

    const nameEng = targets.filter(item => {
      if (item && item.key === target) {
        return item;
      }
    })[0];
    if (nameEng) {
      this.setState({
        target,
        nameEng
      });
    } else {
      alert("error");
    }

    self.setState({
      showError: false,
      viewNoty: "process"
    });
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (this.state.fileList.length) {
          functions.getBase64(fileList[0], function(err, result) {
            if (!err) {
              let aux = values.target.split("-");
              let form = {
                process: {
                  id: aux[0],
                  name: selected.name
                },
                engine: nameEng,
                source: {
                  id: aux[0],
                  name: selected.name
                },
                file: result,
                email: values.email
              };
              self.setState({
                uploading: true
              });
              sendFile(form)
                .then(response => {
                  message.success("This is a message of success");
                  self.setState({
                    uploading: false,
                    viewNoty: "success",
                    fileList: []
                  });
                })
                .catch(error => {
                  console.log(error);
                  message.error(error.message);

                  self.setState({
                    uploading: false,
                    viewNoty: "error"
                  });
                });
            }
          });
        } else {
          self.setState({
            showError: true
          });
        }
      }
    });
  };

  handleChange = value => {
    const { engines } = this.state;
    engines.forEach(element => {
      if (element.id.toString() === value) {
        this.setState({
          selected: element
        });
      }
    });
    this.setState({ value });
  };

  changeSource = source => {
    this.setState({
      source
    });
  };

  changeTarget = target => {
    this.setState({
      target
    });
  };

  getSources = () => {
    const {
      selected: { engines }
    } = this.state;
    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.source) {
          flag = true;
          break;
        }
      }
      if (!flag) {
        let key = element.source;
        array.push({ key, name: functions.languages[key] });
      }
    });
    return array;
  };

  getTargets = () => {
    const {
      source,
      selected: { engines }
    } = this.state;

    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.target) {
          flag = true;
          break;
        }
      }

      if (source) {
        if (!flag && element.source === source) {
          if (element.domain) {
            element["key"] = `${element.id}-${element.target}`;
            element["text"] = `${functions.languages[element.target]} - ${
              element.name
            } - ${element.domain}`;
          } else {
            element["key"] = `${element.id}-${element.target}`;
            element["text"] = `${functions.languages[element.target]} - ${
              element.name
            }`;
          }
          array.push(element);
        }
      }
    });
    return array;
  };

  changeEmail = email => {
    this.setState({
      email
    });
  };

  renderForms = () => {
    const {
      form: { getFieldDecorator, getFieldsError }
    } = this.props;

    const {
      fileList,
      uploading,
      processEnd,
      showError,
      source,
      target,
      loading,
      engines,
      selected,
      email
    } = this.state;

    const formItemLayout = {
      labelCol: {
        span: { span: 24 }
      },
      wrapperCol: {
        span: { span: 24 }
      }
    };
    const formItemLayout2 = {
      wrapperCol: {
        xs: { span: 24 }
      }
    };

    const props = {
      onRemove: file => {
        if (!processEnd) {
          this.setState(state => {
            const index = state.fileList.indexOf(file);
            const newFileList = state.fileList.slice();
            newFileList.splice(index, 1);
            return {
              fileList: newFileList
            };
          });
        }
      },
      beforeUpload: file => {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!functions.checkFile(file)) {
          message.error("You can only upload (docx|xlsx|pptx|odt) file!");
        } else if (!isLt2M) {
          message.error("Image must smaller than 2MB!");
        } else {
          this.setState(state => ({
            fileList: [...state.fileList, file]
          }));
        }

        return false;
      },
      disabled: fileList.length,
      multiple: false,
      fileList
    };
    let options = [];
    if (engines.length) {
      options = engines.map(d => (
        <Option key={d.id}>
          {d.name.replace(/^\w/, c => c.toUpperCase())}
        </Option>
      ));
    }

    return (
      <Form {...formItemLayout} onSubmit={this.handleSubmit}>
        <Row type="flex" justify="center" align="middle">
          <Col xs={24}>
            <FormItemWrapper {...formItemLayout2}>
              <Div error={showError && !fileList.length}>
                <Dragger {...props}>
                  <p className="ant-upload-drag-icon">
                    <Icon type="inbox" />
                  </p>
                  <p className="ant-upload-text">
                    Click or drag file to this area to upload
                  </p>
                </Dragger>
                {showError && !fileList.length ? (
                  <Alert
                    style={{ margin: 5 }}
                    message="You must upload a file before continuing!!"
                    type="error"
                    showIcon
                  />
                ) : null}
              </Div>
            </FormItemWrapper>
          </Col>
          <Col xs={24}>
            <FormItemWrapper
              validateStatus={loading ? "validating" : ""}
              label="Process type"
              hasFeedback
            >
              {getFieldDecorator("process", {
                disabled: source === null || processEnd,
                rules: [
                  {
                    required: true,
                    message: "Please select your Process!"
                  }
                ]
              })(
                <Select
                  autofocus="true"
                  ref={c => (this.state._input = c)}
                  onChange={this.handleChange}
                >
                  {options}
                </Select>
              )}
            </FormItemWrapper>
          </Col>

          <Col xs={24}>
            <FormItemWrapper label="Source Language" hasFeedback>
              {getFieldDecorator("source", {
                disabled: processEnd,
                rules: [
                  {
                    required: true,
                    message: "Please select your Language of origin!"
                  }
                ]
              })(
                <Select
                  disabled={!this.getSources().length}
                  onChange={this.changeSource}
                >
                  {this.getSources().map(element => {
                    return <Option value={element.key}>{element.name}</Option>;
                  })}
                </Select>
              )}
            </FormItemWrapper>
            <FormItemWrapper label="Target Language" hasFeedback>
              {getFieldDecorator("target", {
                disabled: source === null || processEnd,
                rules: [
                  {
                    required: true,
                    message: "Please select your Exit language!"
                  }
                ]
              })(
                <Select
                  onChange={this.changeTarget}
                  disabled={source === null || processEnd}
                >
                  {this.getTargets().map(element => {
                    return <Option value={element.key}>{element.name}</Option>;
                  })}
                </Select>
              )}
            </FormItemWrapper>
            <FormItemWrapper
              label="Notification email"
              hasFeedback
              help="The result of the process will be notified via email."
            >
              {getFieldDecorator("email", {
                disabled: processEnd,
                rules: [
                  {
                    type: "email",
                    message: "The input is not valid E-mail!"
                  },
                  {
                    required: true,
                    message: "Please input your E-mail!"
                  }
                ]
              })(
                <Input
                  onChange={this.changeEmail}
                  disabled={!selected.engines.length}
                />
              )}
            </FormItemWrapper>
          </Col>
          <Col xs={24}>
            <FormItemWrapper
              style={{
                display: "flex",
                justifyContent: "center",
                marginBottom: 0
              }}
            >
              {!processEnd ? (
                <Row justify="center" type="flex">
                  <Button
                    style={{ marginTop: 10 }}
                    icon="upload"
                    type="primary"
                    htmlType="submit"
                    size="large"
                    loading={uploading}
                    disabled={
                      !this.getSources().length ||
                      !target ||
                      !source ||
                      !email ||
                      functions.hasErrors(getFieldsError())
                    }
                  >
                    {uploading ? "Uploading" : "Start Upload"}
                  </Button>
                </Row>
              ) : null}
            </FormItemWrapper>
          </Col>
        </Row>
      </Form>
    );
  };

  render() {
    const { viewNoty } = this.state;
    if (viewNoty === "process") {
      return this.renderForms();
    }
    if (viewNoty === "error") {
      return (
        <DivError>
          <IL
            src="/images/undraw_bug_fixing_oc7a.svg"
            loading={() => <div>Loading...</div>}
            error={() => <div>Error</div>}
          />
          <h2> Hmm, something went wrong</h2>
          <p>
            so it looks like your transfer didn't work – please give it another
            go
          </p>
          <Divider />
          <DivButtons>
            <Button
              onClick={() => {
                this.setState({ viewNoty: "process" });
              }}
              type="primary"
              size="large"
            >
              Try Again
            </Button>
          </DivButtons>
        </DivError>
      );
    }
    if (viewNoty === "success") {
      return (
        <DivError>
          <IL
            src="/images/undraw_confirmation_2uy0.svg"
            loading={() => <div>Loading...</div>}
            error={() => <div>Error</div>}
          />
          <h2> You’re done!</h2>
          <p>
            Soon you will receive a notification to the mail to notify the
            completion of the process.
          </p>
          <Divider />
          <DivButtons>
            <Button
              onClick={() => {
                this.setState({ viewNoty: "process" });
              }}
              type="primary"
              size="large"
            >
              Send another?
            </Button>
          </DivButtons>
        </DivError>
      );
    }
  }
}

const WrappedProcess = Form.create()(Process);

export default WrappedProcess;
