import React, { Component } from "react";
import { Row, Col, Card } from "antd";
import styled from "styled-components";
import Process from "./Process";

const RowWrapper = styled(Row)`
  min-height: 100vh;
`;
const CardWrapper = styled(Card)`
  width: 90%;
  height: 88%;
  background: #e4edf2 !important;
  border-color: #b4d8e4 !important;
`;

export default class FreeUser extends Component {
  _selectProcess = value => {
    const { selectProcess } = this.props;
    selectProcess(value);
  };
  // xs={20} sm={16} md={12} lg={8} xl={4}
  render() {
    const { process, changeFileID } = this.props;
    return (
      <RowWrapper type="flex" justify="center">
        <Col
          xs={22}
          sm={16}
          md={12}
          lg={8}
          xl={6}
          style={{ display: "flex", justifyContent: "center" }}
        >
          {/* <Spin spinning={process.loading} tip="Loading..."> */}
          <CardWrapper>
            <Process
              changeFileID={changeFileID}
              process={process}
              selectProcess={this._selectProcess}
            />
          </CardWrapper>
          {/* </Spin> */}
        </Col>
        <Col xs={22} sm={8} md={12} lg={16} xl={18} />
      </RowWrapper>
    );
  }
}
